import {
  FC,
  ReactNode,
  createContext,
  useMemo,
} from 'react';
import { useParams } from 'react-router-dom';
import { tabs } from '../services/tabs';

type TTabsContext = {
  children: ReactNode;
};

interface ITabsProvider {
  tabId: string | undefined,
  tabContent: string,
}

export const TabsProvider = createContext<ITabsProvider>({
  tabId: '',
  tabContent: '',
});

export const TabsContext: FC<TTabsContext> = ({ children }) => {
  const { tabId } = useParams();

  const tabContent = tabs
    .find(tab => tab.id === tabId)?.content ?? 'Please select a tab';

  const initialValue = useMemo(() => ({
    tabContent,
    tabId,
  }), [tabContent, tabId, tabs]);

  return (
    <TabsProvider.Provider value={initialValue}>
      {children}
    </TabsProvider.Provider>
  );
};
